import { groupBy, reduce } from 'lodash'
import React, { useCallback, useMemo } from 'react'
import styled from 'styled-components'
import * as XLSX from 'xlsx'
import { format } from 'date-fns'

import Button from '../components/Button'
import Box from '../components/Box'

const Table = styled.table`
width: 100%;
th, td {
  padding: 0.5em;
}
th {
  border: 1px solid lightGray;
  border-bottom-width: 2px;
}

td {
  border: 1px solid lightGray;
}
`

const heading = [
  'slide',
  'title',
  'part no.',
  'update(s)',
]

const getGouped = (obj, by) => Object.values(groupBy(obj, by))

const CompareSummary = ({ data, headerRow, compareMark, comparePages, sheetName }) => {
  const headers = useMemo(() => {
    return headerRow.slice(1, headerRow.length - 1)
      .reduce((all, h) => {
        if (h.columns) return all.concat(h.columns.map(c => `${h.Header}/${c.Header}`))
        return all.concat(h.Header)
      }, [])
  }, [headerRow])
  const groupedSummary = useMemo(() => {
    const pageTitles = []
    const markClone = Object.assign({}, compareMark)
    let result = data.reduce((all, page, p) => {
      pageTitles.push(page[0][0])

      return page.reduce((a, row) => {
        const toCompare = markClone?.[row[1]]
        if (toCompare) {
          delete markClone[row[1]]
        }
        const compared = toCompare ? row.reduce((res, r, i) => {
          if (i > 1 && i < headers.length && r !== toCompare[i]) {
            const h = headers[i - 2]
            if (h && r) {
              res.push(`${h}: ${r}`.replace(/\n/g, ' '))
            }
          }
          return res
        }, []) : ['New!']
        compared.forEach(c => {
          a.push([
            p,
            row[1],
            c,
          ])
        })

        return a
      }, all)
    }, [])
    const deletedRecs = groupBy(Object.keys(markClone), k => comparePages[k])
    result = reduce(deletedRecs, (res, r, p) => res.concat(r.map(k => [p - 1, k, 'x Removed'])), result)
    const groupedByPage = getGouped(result, 0).sort((a, b) => a[0] > b[0])
    return groupedByPage.map((page) => getGouped(page.map(row => [`P.${row[0] + 1}`, pageTitles[row[0]], ...row.slice(1)]), 2))
  }, [compareMark, data, headers, comparePages])
  const handleExcel = useCallback(() => {
    const elt = document.getElementById('changes-table');
    const sheet = 'Sheet 1'
	  const wb = XLSX.utils.table_to_book(elt, { sheet });
    // const ws = wb.Sheets[sheet]
    // const [, end] = ws['!ref'].split(':')
    // const [, col, row] = /^([A-Z]+)(\d+)$/.exec(end)
    // console.log(ws)
    // range(row - 1).forEach(i => {
    //   ws[`${col}${i + 1}`].v = ws[`${col}${i + 1}`].v.replace(/\n/g, '\r\n')
    //   console.log(ws[`${col}${i + 1}`].v)
    //   ws[`${col}${i + 1}`].r = `<t>${ws[`${col}${i + 1}`].v}</t><phoneticPr fontId="1" type="noConversion"/>`
    // })
    XLSX.writeFile(wb, `${sheetName}-updates-${format(new Date(), 'yyyyMMddHHmm')}.xlsx`);
  }, [sheetName])
  // console.log(groupedSummary)
  return (
    <div>
      <Box textAlign="right" mb="1em">
        <Button onClick={handleExcel}>匯出Excel</Button>
      </Box>
      <Table id="changes-table">
        <thead>
          <tr>
            {heading.map((h, i) => <th key={i}>{h}</th>)}
          </tr>
        </thead>
        <tbody>
          {groupedSummary.map((rows, a) => {
            const pageSpan = rows.reduce((count, r) => count + r.length, 0)
            // console.log(rows, pageSpan)
            return rows.map((partGroup, b) => partGroup.map((part, c) => (
              <tr key={`${a}${b}${c}`}>
                {part.map((col, d) => (d === 3 || (b > 0 && c === 0 && d >= 2) || (c === 0 && b === 0)) && (
                  <td
                    key={`${a}${b}${c}${d}`}
                    rowSpan={(d < 2) ? pageSpan : (d === 2 ? partGroup.length : 1)}
                  >
                    {col}
                    {/* ({[a,b,c,d].join()}) */}
                  </td>
                ))}
              </tr>
            )))
          })}
        </tbody>
      </Table>
    </div>
  )
}

export default CompareSummary
