import React, { useMemo, useEffect, useRef, useState, createElement } from 'react'
import findLastIndex from 'lodash/findLastIndex'
import { FaCheckCircle } from 'react-icons/fa'
import { MdRemove, MdClear } from 'react-icons/md'
import colorParse from 'color-parse'

import Circle from 'components/Circle'
import theme from 'components/ThemeProvider/theme'
import Box from 'components/Box'

export const useOrginizedHeader = headerGroups => useMemo(() => {
  const placeHolders = {}
  return headerGroups.reduce((headers, headerGroup, i) => {
    headers[i] = headerGroup.headers.map((header, j) => {
      if (header.placeholderOf) {
        placeHolders[header.placeholderOf.id] = [i, j]
        return {}
      } else if (placeHolders[header.id]) {
        const [x, y] = placeHolders[header.id]
        headers[x][y] = { rowSpan: i - x + 1, children: header.render('Header') }
        return {}
      } else {
        return { colSpan: header.totalVisibleHeaderCount, children: header.render('Header') }
      }
    })
    return headers
  }, [])
}, [headerGroups])

export const useHeaderChildrenIndex = headerGroups => useMemo(() => headerGroups.reduce((ci, groups) => {
  groups.headers.forEach(header => {
    if (header.columns) {
      const hid = header.originalId || header.id
      ci[hid] = header.columns.reduce((ids, col, i) => {
        const colId = col.originalId || col.id
        ids[colId] = i
        return ids
      }, {})
    }
  });
  return ci
}, {}), [headerGroups])

const alldPttn = /^\d+$/
const nmPttn = /\d+"\w"/
const yearPttn = /([1-4]Q)(\d{2})|(\d{2})(Q[1-4])/i
const cfgPttn = /x\d+(\/x\d+)+/

export const parseValue = value => {
  if (yearPttn.test(value)) {
    return value.replace(yearPttn, '$1\n$2')
  }
  if (nmPttn.test(value)) {
    return value.replace(/\d+/, '$&\n')
  }
  if (cfgPttn.test(value)) {
    return value.replace(/\//g, '$&\n')
  }
  return value
}

const toYearFormat = (y, q) => [2000 + y * 1, q.replace(/\D+/, '') * 1]

export const parseYear = value => {
  if (alldPttn.test(value)) {
    return [2000 + value * 1, 4]
  }
  if (yearPttn.test(value)) {
    const a = value.substring(0, 2)
    const b = value.substring(2)
    if (alldPttn.test(a)) {
      return toYearFormat(a, b)
    }
    return toYearFormat(b, a)
  }
  return value
}

const checkPttn = /^y|(yes)|(now)$/i
const crossPttn = /^n\/?a$/i
const noPttn = /^n|(no)$/i

const iconStyle = { size: '1.25em', style: { margin: '0.5em 0' } }

const withRecolor = SubComp => props => {
  const ref = useRef()
  const [color, setColor] = useState('currentColor')
  useEffect(() => {
    const currentColor = colorParse(window.getComputedStyle(ref.current).color)
    if (currentColor.values.every(v => v === 0)) {
      setColor(theme.colors.lightGray)
    }
  }, [])
  return (
    <Box.Inline color={color} ref={ref}>
      <SubComp {...props} />
    </Box.Inline>
  )
}

const parseCell = cell => {
  if (!cell.value) return null
  if (cell.value === '-' || cell.value === '--') {
    return createElement(withRecolor(MdRemove), { ...iconStyle, color: 'currentColor' })
  }
  if (checkPttn.test(cell.value)) {
    return createElement(FaCheckCircle, { ...iconStyle, color: 'currentColor' })
  }
  if (crossPttn.test(cell.value.replace('\n', ''))) {
    return createElement(withRecolor(MdClear), { ...iconStyle, color: 'currentColor' })
  }
  if (noPttn.test(cell.value)) {
    return createElement(Circle, { width: "1.25em", my: "0.5rem", bg: "currentColor", opacity: "0.3" })
  }
  return cell.render('Cell')
}

export const useOriginizedRows = ({
  rows,
  prepareRow,
  allData,
  updatedMark,
}) => useMemo(() => {
  return rows.reduce((allRows, row, i) => {
    prepareRow(row)
    const thisRow = row.cells.map((cell, j) => {
      if (cell.value) {
        return {
          children: parseCell(cell),
          rowSpan: 1,
          updated: updatedMark ? cell.column.accessor(updatedMark[i]) : false,
        }
      } else if (i === 0) {
        const parentIndex = allData.findIndex(r => r[j + 1])
        return {
          children: cell.column.accessor(allData[parentIndex]),
          rowSpan: 1,
          updated: updatedMark ? cell.column.accessor(updatedMark[parentIndex]) : false,
        }
      } else {
        const parentIndex = findLastIndex(allRows, r => r[j].rowSpan)
        if (parentIndex > -1) allRows[parentIndex][j].rowSpan += 1
        return {}
      }
    })
    return allRows.concat([thisRow])
  }, [])
}, [rows, prepareRow, allData, updatedMark])
