import React, { useMemo, useRef, useEffect, useCallback, createRef, forwardRef } from 'react';
import { useTable } from 'react-table'
import styled, { createGlobalStyle, css } from 'styled-components'
import last from 'lodash/last'
import isObject from 'lodash/isObject'
import range from 'lodash/range'
import get from 'lodash/get'
import { saveAs } from 'file-saver'
import composeRefs from '@seznam/compose-react-refs'

import 'vendor/Blob'
import 'vendor/canvas-toBlob'

import Box from 'components/Box'
import Flex from 'components/Flex'
import BackgroundImage from 'components/BackgroundImage'
import Button from 'components/Button'
import theme from 'components/ThemeProvider/theme'

import Dom2Canvas from 'utils/dom2canvas'
import {
  useOrginizedHeader,
  useHeaderChildrenIndex,
  useOriginizedRows,
  parseValue,
  parseYear,
} from './helpers'
import Timeline from './timeline'

import colors from './colors'
import bg from './bg.png'

const clearChildren = dom => {
  dom.innerHTML = ''
}

const CanvasContainer = styled(Box.FullAbsolute)`
  canvas {
    width: 100%;
    height: auto;
  }
`

const headerBar = css`
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  height: 8px;
  background: ${theme.colors.primary};
`

const TableStyle = createGlobalStyle`
  table.render-table {
    line-height: 1.375;
    font-family: ${theme.font};
    tr:last-child > th {
      position: relative;
      &::before {
        ${headerBar}
        top: 0;
      }
      &::after {
        ${headerBar}
        bottom: 2px;
      }
    }
    tr > th,
    tr > td {
      height: 52px;
    }
  }
`

const WIDTH = 1920
const HEIGHT = 1080
const borderGap = 9
const borderLine = 3
const timelinePadding = 6
const timelineOffsetX = 2
const timelineOffsetY = 12

const rotateStyle = {
  writingMode: 'vertical-rl',
  textOrientation: 'mixed',
  transform: 'rotate(180deg)',
}

const getColor = name => {
  const [key] = name.split('\n')
  const [colorKey] = key.split('/')
  return colors[colorKey] || colors.fallback
}

const DecoLine = styled.div`
  position:absolute;
  right: -${borderGap / 2 + 1}px;
  top: 16px;
  bottom: 16px;
  width: 2px;
  background: ${theme.colors.primary};
`

const timelinePttn = /^\[tl/;
const timelineStartPttn = /^\[tl start/;
const timelineEndPttn = /^\[tl end/;
const ddrPattn = /^DDR/

const getYearsHeader = (startYear) => ({
  Header: 'T',
  columns: range(7).map(i => {
    const y = i + startYear
    return {
      Header: y,
      id: y,
      accessor: (d) => {
        const { length } = d
        const start = d[length - 3]
        const end = d[length - 2]
        if (y === start[0]) {
          return '[tl start]'
        } else if (y === end[0]) {
          return '[tl end]'
        } else if (y > start[0] && (y < end[0] || !end)) {
          return '[tl]'
        }
        return '[tl]'
      },
    }
  }),
})

const ensureValue = data => h => data.some(d => h.accessor(d))

const RenderTable = forwardRef(({
  data,
  headerRow,
  allData,
  page,
  updatedMark,
  sheetName,
  compareMode,
  onCanvasLoaded,
  startYear,
}, ref) => {
  const tableRef = useRef()
  const container = useRef()
  const columns = useMemo(() => {
    const cols = headerRow.slice(1, headerRow.length - 1)
      .map((Header, i) => {
        const h = isObject(Header) ? Object.assign({}, Header) : { Header, accessor: d => d[i], id: i }
        return h.columns ? { ...h, columns: Header.columns.filter(ensureValue(data)) } : h
      })
      .filter(h => {
        if (h.columns) return true
        return ensureValue(data)(h)
      })
      .concat(getYearsHeader(startYear))
      // .concat({ Header: '', id: 'dummy', accessor: () => '' })

    return cols
  }, [headerRow, startYear])
  const techCol = useMemo(() => columns.find(col => /^Technology/.test(col.Header)), [columns])
  // const voltCol =(() => columns.find(col => /^Voltage/.test(col.Header)), [columns])
  // const isDRAM = useMemo(() => columns.some(col => /^Configuration/.test(col.Header)), [columns])
  // const groupStart = useMemo(() => columns.findIndex(col => col.columns), [columns])
  const parsedData = useMemo(() => {
    return data.reduce(([res, prevGroupName], row, i) => {
      const rowCount = row.length
      // const colorGroupName = (isDRAM ? techCol.accessor(row) : voltCol.accessor(row)) || prevGroupName
      const colorGroupName = techCol.accessor(row) || prevGroupName
      const mapped = row.map((v, i) => {
        if (i >= rowCount - 3 && i < rowCount - 1) return parseYear(v)
        return i > 1 ? parseValue(v) : v
      })
      mapped.color = getColor(colorGroupName)
      res[i] = mapped

      return [res, colorGroupName]
    }, [[]])[0]
  }, [data, techCol])
  const timelineContainerRef = useRef()
  const timelineStartRef = useRef()
  const timelineEndRef = useRef()
  const timlineRefs = useMemo(() => range(parsedData.length).map(() => ({
    start: { ref: createRef() },
    end: { ref: createRef() },
  })), [parsedData])

  const notes = useMemo(() => data.reduce((all, row) => {
    const note = last(row)
    if (note) {
      return all.concat(note.split('\n'))
    }
    return all
  }, []), [data])

  const tableInstance = useTable({
    columns,
    data: parsedData,
  })

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = tableInstance

  const orginizedHeader = useOrginizedHeader(headerGroups)
  const headerChildrenIndex = useHeaderChildrenIndex(headerGroups)
  const orginizedRows = useOriginizedRows({
    rows,
    prepareRow,
    allData,
    updatedMark,
  })

  const handleDownload = useCallback(() => new Promise((res) => {
    const date = new Date().toLocaleDateString()
    container.current.querySelector('canvas').toBlob((blob) => {
      saveAs(blob, `${date}-${sheetName}-${page + 1}.png`);
      res()
    });
  }), [container, page])

  useEffect(() => {
    if (ref) ref.current = { download: handleDownload }
    const { length } = parsedData[0]
    const containerBox = tableRef.current.getBoundingClientRect()
    const timelineStartBox = timelineStartRef.current.getBoundingClientRect()
    const timelineEndBox = timelineEndRef.current.getBoundingClientRect()
    const timelineBounds = {
      left: Math.round(timelineStartBox.left - containerBox.left) + timelineOffsetX,
      top: Math.round(timelineStartBox.top - containerBox.top + timelinePadding) - timelineOffsetY,
      width: Math.round(timelineEndBox.right - timelineStartBox.left) - timelineOffsetX,
      height: Math.round(timelineEndBox.bottom - timelineStartBox.top - timelinePadding) + timelineOffsetY,
    }
    clearChildren(timelineContainerRef.current)
    const labelPos = timelineBounds.left + timelineBounds.width / 3
    const tl = new Timeline(WIDTH, HEIGHT, timelineBounds)
    timlineRefs.forEach((refs, i) => {
      const start = parsedData[i][length - 3]
      const end = parsedData[i][length - 2]
      const startSec = start && start[1] && start[0] >= startYear ? (start[1] - 1) / 4 : 0
      const endSec = end && end[1] ? end[1] / 4 : 1
      const startRect = refs.start.ref.current.getBoundingClientRect()
      const endRect = refs.end.ref.current.getBoundingClientRect()
      const x = Math.round(startRect.left + startRect.width * startSec - containerBox.left + timelineOffsetX)
      const y = Math.round(startRect.top - containerBox.top + timelinePadding)
      const width = Math.round(endRect.left - containerBox.left - x + endRect.width * endSec)
      const height = Math.round(endRect.bottom - containerBox.top - y)
      tl.add(x, y, width, height, parsedData[i].color, !refs.end.added)
      tl.addLabel(
        labelPos,
        y + height / 2,
        parsedData[i][1],
      )
    })
    const svg = tl.getSVG()
    timelineContainerRef.current.appendChild(svg)
    const ins = new Dom2Canvas(tableRef.current, WIDTH, HEIGHT)
    ins.toCanvas((canvas) => {
      // presentational style
      canvas.style.width = '100%'
      canvas.style.height = 'auto'
      if (container.current) {
        clearChildren(container.current)
        container.current.appendChild(canvas)
        setTimeout(onCanvasLoaded)
      }
    })
    const dom = container.current
    return function cleanup() {
      clearChildren(dom)
    }
  }, [page, startYear, compareMode])
  return (
    <Box.Relative my="2em">
      <TableStyle />
      <BackgroundImage ratio={9 / 16} src={bg}>
        <CanvasContainer ref={container} />
      </BackgroundImage>
      <Button my="0.5em" onClick={handleDownload}>下載本頁圖檔</Button>
      <Box.Absolute
        opacity={0}
        height={0}
        overflow="hidden"
      >
        <Box.Relative
          width={WIDTH}
          height={HEIGHT}
          lineHeight="1.5"
          ref={tableRef}
        >
          <table
            className="render-table"
            style={{
              position: 'absolute',
              top: 60 * 2,
              left: 32 * 2,
              right: 48 * 2,
            }}
            {...getTableProps()}
          >
            <thead>
              {headerGroups.map((headerGroup, i) => {
                const hgLength = headerGroup.headers.length
                return (
                  <tr {...headerGroup.getHeaderGroupProps()}>
                    {// Loop over the headers in each row
                    headerGroup.headers.map((column, j) => {
                      const { children, colSpan, ...rest } = orginizedHeader[i][j]
                      if (children === 'T') {
                        return (
                          <Box
                            as="th"
                            {...column.getHeaderProps()}
                            colSpan={colSpan}
                            fontSize="16px"
                            color="primary"
                            fontWeight="400"
                            textAlign="left"
                            px="1em"
                            width="30%"
                          >
                            Product schedules are subject to change, please contact Winbond sales representative for latest updates.
                          </Box>
                        )
                      }
                      const isYear = get(column, ['parent', 'Header']) === 'T'
                      return children ? (
                        <Box
                          as="th"
                          {...column.getHeaderProps()}
                          {...rest}
                          colSpan={colSpan}
                          style={{
                            backgroundColor: 'white',
                            backgroundImage: i < 1 && `linear-gradient(${theme.colors.primary} ${colSpan > 1 ? 90 : 70}%, rgba(14,78,110,0.5) 95%, transparent 100%)`,
                          }}
                          color={i < 1 ? 'white' : 'primary'}
                          py="1em"
                          px={i < 1 ? '0.25em' : 0}
                          fontWeight="bold"
                          fontSize="24px"
                          border={`${borderGap}px solid white`}
                          width={colSpan > 1 ? '35%' : 'auto'}
                        >
                          <Box.Inline
                            whiteSpace="pre"
                            style={colSpan > 1 ? {} : rotateStyle}
                          >
                            {children}
                          </Box.Inline>
                          {j < hgLength - 1
                            && (
                              isYear
                              || (column.depth > 0 && (() => {
                                const pos = get(headerChildrenIndex, [column.parent.id, column.id])
                                return pos % 2 === 1 && pos !== column.parent.columns.length - 1
                              })())
                            )
                            && <DecoLine />}
                        </Box>
                      ) : null
                    })}
                  </tr>
                )
              })}
            </thead>
            {/* Apply the table body props */}
            <tbody {...getTableBodyProps()}>
              {// Loop over the table rows
              rows.map((row, i) => {
                return (
                  // Apply the row props
                  <tr {...row.getRowProps()}>
                    {// Loop over the rows cells
                    row.cells.map((cell, j) => {
                      // Apply the cell props
                      const { children, updated, ...rest } = orginizedRows[i][j]
                      if (timelinePttn.test(cell.value)) {
                        let ref
                        const isFirst = headerChildrenIndex[cell.column.parent.id][cell.column.id] === 0
                        const isLast = j === row.cells.length - 1
                        if (timelineStartPttn.test(cell.value)) {
                          ref = timlineRefs[i].start.ref
                        } else if (isFirst) {
                          ref = timlineRefs[i].start.ref
                        }
                        if (timelineEndPttn.test(cell.value)) {
                          ref = ref ? composeRefs(ref, timlineRefs[i].end.ref) : timlineRefs[i].end.ref
                          timlineRefs[i].end.added = true
                        }
                        if (isLast && !timlineRefs[i].end.added) {
                          ref = timlineRefs[i].end.ref
                        }
                        if (i === 0 && isFirst) {
                          ref = ref ? composeRefs(ref, timelineStartRef) : timelineStartRef
                        }
                        if (i === rows.length - 1 && isLast) {
                          ref = ref ? composeRefs(ref, timelineEndRef) : timelineEndRef
                        }
                        return <td {...cell.getCellProps()} {...rest} ref={ref} />
                      }
                      // const line = String(cell.column.Header).split('\n').length
                      // (cell.column.parent ? (
                      //   <Box
                      //     as="td"
                      //     bg="gray"
                      //     borderWidth={`${borderLine}px ${borderGap}px`}
                      //     borderStyle="solid"
                      //     borderColor="white"
                      //   />
                      // ) : null)
                      return children ? (
                        <Box
                          as="td"
                          {...cell.getCellProps()}
                          {...rest}
                          fontSize={20}
                          whiteSpace="pre"
                          textAlign="center"
                          bg="gray"
                          color={updated ? 'red' : 'black'}
                          borderWidth={`${borderLine}px ${borderGap}px`}
                          borderStyle="solid"
                          borderColor="white"
                          lineHeight="1.125"
                          py="0.25em"
                          // width={line > 1 ? 106 : 70}
                        >
                          <Box.Inline style={ddrPattn.test(cell.value) || cell.column.Header === 'Interface' ? rotateStyle : null}>
                            {children}
                          </Box.Inline>
                        </Box>
                      ) : null
                    })}
                  </tr>
                )
              })}
            </tbody>
          </table>
          <Box
            position="absolute"
            top={60}
            left={64}
            transform="translateY(-50%)"
            fontSize="54px"
            color="darkGray"
            fontFamily={theme.font}
            fontWeight="bold"
          >
            {parsedData[0][0]}
          </Box>
          {notes && notes.length > 0 && (
            <Flex
              fontFamily={theme.font}
              position="absolute"
              left={64}
              bottom={24}
            >
              <Flex
                bg="red"
                p="1em"
                borderRadius="0.5em"
                mr="0.5em"
                color="white"
                fontSize="26px"
                fontWeight="bold"
                alignItems="center"
              >NOTE</Flex>
              <Box as="ul" color="red" fontSize="20px" lineHeight="1.25">
                {notes.map((note, i) => <li key={i}>{note}</li>)}
              </Box>
            </Flex>
          )}
          <div ref={timelineContainerRef} />
        </Box.Relative>
      </Box.Absolute>
    </Box.Relative>
  )
});

RenderTable.defaultProps = {
  onCanvasLoaded: () => {},
}

export default RenderTable;
