import React, { useState, useMemo } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import size from 'lodash/size'
import { Link as GatsbyLink } from 'gatsby'

import Container from '../components/Container'
import Button from '../components/Button'
import Pagination from '../components/Pagination'
// import { responsive } from '../components/ThemeProvider/theme';

import SheetDataTable from './SheetDataTable'
import PageControl from './PageControl'
import Downloader from './Downloader'

import CompareSummary from './CompareSummary';
import SheetLoader from './SheetLoader';
import Link from 'components/Link';
import useTableHeader from 'hooks/useTableHeader';
import useYearControl from 'hooks/useYearControl';

// const colorKeys = [{}, {}]
// const getColor = (name, i = 0) => {
//   const keys = colorKeys[i]
//   if (typeof keys[name] !== 'number') {
//     keys[name] = (max(Object.values(keys)) || 0) + 1
//   }
//   return keys[name]
// }

const SheetData = ({ data, compareSheetData, selectedSheet }) => {
  const [downloading, setDownloading] = useState()
  const { startYear, yearControl } = useYearControl()
  // const [selectedRow, setSelectedRow] = useState({})
  const [page, setPage] = useState(0)

  const doubleHeader = useMemo(() => data && data[0].some(h => !h), [data])
  const headerRow = useTableHeader(data)
  const tableData = useMemo(() => data ? data.slice(doubleHeader ? 2 : 1) : [], [data, doubleHeader])
  const pages = useMemo(() => {
    return tableData.reduce((all, row) => {
      let i = all.length - 1
      if (row[0]) {
        i = all.push([]) - 1
      }
      all[i].push(row)
      return all
    }, [])
  }, [tableData])

  const compareData = useMemo(() => (compareSheetData) ? compareSheetData.slice(doubleHeader ? 2 : 1) : [], [compareSheetData, doubleHeader])
  const [compareMark, comparePages] = useMemo(() => {
    if (!size(compareData)) return [null, null]
    let page = 0
    return compareData.reduce(([all, pages], row) => {
      if (row[0]) page += 1
      all[row[1]] = row
      pages[row[1]] = page
      return [all, pages]
    }, [{}, {}])
  }, [compareData])
  return (
    <Tabs style={{ marginTop: '2em' }}>
      <TabList>
        <Tab>簡報產生器</Tab>
        <Tab disabled={!compareData.length}>更新對照表</Tab>
      </TabList>
      <TabPanel>
        {yearControl}
        {pages && pages[page] && (
          <>
            <SheetDataTable
              data={pages[page]}
              headerRow={headerRow}
            // onSelectRow={setSelectedRow}
            />
            <Pagination
              pageCount={pages.length}
              pageRangeDisplayed={5}
              marginPagesDisplayed={2}
              onPageChange={({ selected }) => setPage(selected)}
            />
            <PageControl
              key={selectedSheet}
              allData={tableData}
              headerRow={headerRow.slice(0, headerRow.length - 2)}
              page={page}
              data={pages[page]}
              compareMark={compareMark}
              sheetName={selectedSheet}
              startYear={startYear}
            />
            <Button onClick={() => setDownloading(true)} disabled={downloading}>
              批次下載{downloading ? '中...' : ` (${pages.length}頁)`}
            </Button>
            {downloading && (
              <Downloader
                allData={tableData}
                headerRow={headerRow.slice(0, headerRow.length - 2)}
                pages={pages}
                compareMark={compareMark}
                sheetName={selectedSheet}
                startYear={startYear}
                onDownloaded={() => setDownloading(false)}
              />
            )}
          </>
        )}
      </TabPanel>
      <TabPanel>
        <CompareSummary
          data={pages}
          headerRow={headerRow}
          compareMark={compareMark}
          sheetName={selectedSheet}
          comparePages={comparePages}
        />
      </TabPanel>
    </Tabs>
  )
}

const MarketingPage = () => {
  return (
    <Container>
      <Link as={GatsbyLink} to="/">
        {`< Home`}
      </Link>
      <SheetLoader name="marketing">
        <SheetData />
      </SheetLoader>
    </Container>
  )
}

export default MarketingPage
