import React, { useState, useRef } from 'react'

// refs.reduce((prev, ref) => prev.then(ref.current.download), Promise.resolve())

import PageControl from './PageControl'

const Downloader = ({
  allData,
  headerRow,
  pages,
  compareMark,
  sheetName,
  onDownloaded,
  startYear,
}) => {
  const [activePage, setActivePage] = useState(0)
  const ref = useRef()
  return (
    <PageControl
      key={activePage}
      allData={allData}
      headerRow={headerRow}
      page={activePage}
      data={pages[activePage]}
      compareMark={compareMark}
      ref={ref}
      sheetName={sheetName}
      visible={false}
      startYear={startYear}
      onCanvasLoaded={() => {
        ref.current.download().then(() => {
          if (activePage < pages.length - 1) {
            setActivePage(activePage + 1)
          } else {
            onDownloaded()
          }
        })
      }}
    />
  )
}

export default Downloader
