import range from 'lodash/range'
import size from 'lodash/size'

import theme from '../../components/ThemeProvider/theme'

const parenthesisPttn = /\([^)]+\)/

const xmlns = 'http://www.w3.org/2000/svg';

class Timeline {
  constructor(w, h, bounds, n = 7) {
    this.svg = document.createElementNS(xmlns, 'svg');
    this.svg.style.position = 'absolute'
    this.svg.style.top = 0
    this.svg.style.left = 0
    this.svg.setAttribute('width', w)
    this.svg.setAttribute('height', h)
    this.defs = document.createElementNS(xmlns, 'defs')
    this.svg.appendChild(this.defs)
    this.bounds = bounds
    const secWidth = bounds.width / 7
    this.bounds.right = bounds.left + bounds.width
    this.bounds.bottom = bounds.top + bounds.height
    // this.addGuide(bounds.left, true)
    // this.addGuide(bounds.left + bounds.width, true)
    range(n).forEach(i => {
      const middle = bounds.left + secWidth * (i + 0.5)
      this.addGuide(middle)
      const right = middle + secWidth / 2
      this.addGuide(right, true)
    })
  }

  gradients = {}

  addGuide = (x, thick) => {
    const line = document.createElementNS(xmlns, 'line');
    line.setAttribute('x1', x)
    line.setAttribute('y1', this.bounds.top)
    line.setAttribute('x2', x)
    line.setAttribute('y2', this.bounds.bottom)
    if (thick) {
      line.setAttribute('stroke', theme.colors.primary)
      line.setAttribute('opacity', 0.33)
      line.setAttribute('stroke-width', 2)
    } else {
      line.setAttribute('stroke', '#B5B5B5')
      line.setAttribute('stroke-width', 1)
      line.setAttribute('stroke-dasharray', 2)
    }
    this.svg.appendChild(line)
  }

  saveGradient = (key, gradient) => {
    const id = ['gradient', size(this.gradients) + 1].join('-')
    gradient.setAttribute('id', id)
    this.gradients[key] = `url(#${id})`
    return this.gradients[key]
  }

  getGradient = (fill) => {
    if (this.gradients[fill]) {
      return this.gradients[fill]
    }
    const gradient = document.createElementNS(xmlns, 'linearGradient');
    gradient.appendChild(this.createGradientStop('0%', '#fff'))
    // gradient.appendChild(this.createGradientStop('10%', fill, 0.5))
    // gradient.appendChild(this.createGradientStop('20%', fill))
    gradient.appendChild(this.createGradientStop('50%', fill))
    this.defs.appendChild(gradient)
    return this.saveGradient(fill, gradient)
  }

  createGradientStop = (offset, color, opacity = 1) => {
    const stop = document.createElementNS(xmlns, 'stop');
    stop.setAttribute('offset', offset)
    stop.setAttribute('stop-color', color)
    stop.setAttribute('stop-opacity', opacity)
    return stop
  }

  add = (x, y, w, h, fill = '#cacaca', extend) => {
    const timeline = document.createElementNS(xmlns, 'rect');
    timeline.setAttribute('x', x)
    timeline.setAttribute('y', y)
    timeline.setAttribute('width', w)
    timeline.setAttribute('height', h)
    // timeline.setAttribute('fill', this.getGradient(fill))
    timeline.setAttribute('fill', fill)
    this.svg.appendChild(timeline)
    if (extend) {
      this.addChveron(x + w, y, h, fill)
    }
  }

  addChveron = (x, y, h, fill) => {
    const xOffset = h / 2 * Math.tan(Math.PI / 4)
    const thickness = xOffset * 0.5
    const basePolygon = document.createElementNS(xmlns, 'polygon');
    let points = [
      [x, y],
      [x + thickness * 2, y],
      [x + thickness * 2 + xOffset, y + h / 2],
      [x + thickness * 2, y + h],
      [x, y + h],
    ];
    basePolygon.setAttribute('points', points.map(p => p.join()).join(' '))
    basePolygon.setAttribute('fill', fill)
    this.svg.appendChild(basePolygon)
    const brightPolygon = document.createElementNS(xmlns, 'polygon');
    points = [
      [x, y],
      [x + thickness, y],
      [x + thickness + xOffset, y + h / 2],
      [x + thickness, y + h],
      [x, y + h],
      [x + xOffset, y + h / 2],
    ];
    brightPolygon.setAttribute('points', points.map(p => p.join()).join(' '))
    brightPolygon.setAttribute('fill', '#ffffff')
    brightPolygon.setAttribute('opacity', '0.5')
    this.svg.appendChild(brightPolygon)
  }

  addLabel = (x, y, id) => {
    const idText = document.createElementNS(xmlns, 'text');
    idText.setAttribute('x', x)
    idText.setAttribute('y', y + 10)
    idText.setAttribute('fill', 'black')
    idText.setAttribute('font-size', '22px')
    idText.setAttribute('font-family', theme.font)
    idText.setAttribute('font-weight', 'bold')
    const trimmed = id ? id.replace(/\n|\r\n/g, '').trim() : ''
    const inPranthesis = parenthesisPttn.exec(trimmed)

    if (inPranthesis) {
      idText.textContent = trimmed.replace(inPranthesis[0], '')
      const smallText = document.createElementNS(xmlns, 'tspan');
      smallText.setAttribute('font-size', '16px')
      smallText.textContent = inPranthesis[0]
      idText.appendChild(smallText)
    } else {
      idText.textContent = trimmed
    }
    this.svg.appendChild(idText)
  }

  getSVG = () => this.svg
}

export default Timeline
