import React, { useMemo, forwardRef } from 'react';

import RenderTable from './RenderTable'
import Box from '../components/Box'

const PageControl = forwardRef(({
  allData,
  headerRow,
  data,
  compareMark,
  page,
  visible,
  sheetName,
  onCanvasLoaded,
  startYear,
}, ref) => {
  const updatedMark = useMemo(() => {
    if (!compareMark) return null
    return data.map((row) => {
      const toCompare = compareMark[row[1]]
      if (toCompare) {
        return row.map((r, i) => r !== toCompare[i])
      } else {
        return row.map(() => true)
      }
    })
  }, [data, compareMark])
  // const compareMode = useMemo(() => size(compareMark) > 0, [compareMark])
  if (!data.length) return null
  return (
    <Box
      pointerEvents={!visible && 'none'}
      position={!visible && 'absolute'}
      opacity={!visible && 0}
      height={!visible && 0}
      overflow={!visible && 'hidden'}
    >
      <RenderTable
        allData={allData}
        headerRow={headerRow}
        page={page}
        data={data}
        updatedMark={updatedMark}
        ref={ref}
        sheetName={sheetName}
        // compareMode={compareMode}
        onCanvasLoaded={onCanvasLoaded}
        startYear={startYear * 1}
      />
    </Box>
  );
});

PageControl.defaultProps = {
  visible: true,
}

export default PageControl;
