//       <>
//         <input type="checkbox" ref={resolvedRef} {...rest} />
//       </>
//     )
//   }
// )

// Define a default UI for filtering
function DefaultColumnFilter({
  column: { filterValue, preFilteredRows, setFilter },
}) {
  const count = preFilteredRows.length

  return (
    <StyledInput
      value={filterValue || ''}
      onChange={e => {
        setFilter(e.target.value || undefined) // Set undefined to remove the filter entirely
      }}
      placeholder={`Search ${count} records...`}
    />
  )
}

const defaultColumn = {
  Filter: DefaultColumnFilter,
  canFilter: false,
}

const SheetDataTable = ({ data, headerRow }) => {
  const columns = useMemo(() => {
    const cols = headerRow.map((Header, i) => {
      const col = isObject(Header) ? Object.assign({}, Header) : { Header, accessor: d => d[i], id: i }
      return col
    })
    return cols
  }, [headerRow])
  const tableInstance = useTable({
    columns,
    data,
    defaultColumn,
    // filterTypes,
    initialState: {
      selectedRowIds: data.reduce((sels, r, id) => {
        sels[id] = true
        return sels
      }, {}),
    },
  },
  useFilters,
  // useRowSelect,
  )

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = tableInstance

  // useEffect(() => {
  //   onSelectRow(selectedRowIds)
  // }, [onSelectRow, selectedRowIds])
  // console.log(headerRow)
  return (
    <Box overflowX="auto">
      <StyledTable {...getTableProps()}>
        <thead>
          {// Loop over the header rows
          headerGroups.map(headerGroup => (
            // Apply the header row props
            <tr {...headerGroup.getHeaderGroupProps()}>
              {// Loop over the headers in each row
              headerGroup.headers.map((column, i) => (
                // Apply the header cell props
                <th {...column.getHeaderProps()}>
                  {// Render the header
                  column.render('Header')}
                  <div>{column.canFilter && i === 0 ? column.render('Filter') : null}</div>
                </th>
              ))}
            </tr>
          ))}
        </thead>
        {/* Apply the table body props */}
        <tbody {...getTableBodyProps()}>
          {// Loop over the table rows
          rows.map(row => {
            // Prepare the row for display
            prepareRow(row)
            return (
              // Apply the row props
              <tr {...row.getRowProps()}>
                {// Loop over the rows cells
                row.cells.map(cell => {
                  // Apply the cell props
                  return (
                    <td {...cell.getCellProps()}>
                      {// Render the cell contents
                      cell.render('Cell')}
                    </td>
                  )
                })}
              </tr>
            )
          })}
        </tbody>
      </StyledTable>
    </Box>
  )
};

SheetDataTable.defaultProps = {
  onSelectRow: () => {},
}

export default SheetDataTable;
